import { Anchor, Link, markdown, styled, useBook, useFile, useTitle } from 'topwrite';
import { select, selectAll } from 'unist-util-select';
import { useMemo } from 'react';
import { Heading, Link as MdLink, List } from 'mdast';
import { toString } from 'mdast-util-to-string';
import Header from '@/components/header';

export default function Summary() {

    const file = useFile();
    const bookTitle = useTitle();
    const tree = useMemo(() => {
        return markdown.parse(file.content);
    }, [file.content]);

    const heading = select('heading[depth=1]', tree) as Heading | null;
    const list = select('list', tree) as List | null;
    const { summary } = useBook();

    return <>
        <Header />
        <Container>
            <h1>{toString(heading) || bookTitle}</h1>
            {list && <Featured>
                <h2>Featured</h2>
                <ul>{list.children.map((item, index) => {
                    const link = select('link', item) as MdLink | null;
                    if (link) {
                        const description = selectAll('link+text', item);
                        return <li key={`index-${index}`}>
                            <Anchor href={link.url} base={file.path}>
                                <Bg />
                                <Inner>
                                    <h2>{toString(link)}</h2>
                                    <p>{toString(description)}</p>
                                </Inner>
                            </Anchor>
                        </li>;
                    }
                    return null;
                })}</ul>
            </Featured>}
            <Articles>
                <h2>All articles</h2>
                <ul>{summary.getArticles().map((article, index) => {
                    return <li key={`article-${index}`}><Link to={article}>{article.title}</Link></li>;
                })}</ul>
            </Articles>
        </Container>
    </>;
}

const Container = styled.div`
  display: grid;
  column-gap: 20px;
  row-gap: 100px;
  grid-template-columns: 1fr minmax(auto, 880px) 1fr;
  grid-template-rows: none;
  grid-auto-flow: row;
  height: inherit;
  padding-top: 128px;

  & > * {
    grid-column: 2;
  }
`;

const Bg = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  filter: blur(15px);
  border-radius: 16px;
  transform: scale(1.05) translateZ(0px);
  opacity: 0;
  transition: 0.5s;
`;


const Inner = styled.div`
  border-radius: var(--border-radius-2);
  margin-bottom: 0px;
  overflow: hidden;
  position: relative;
  background: #f6f7f8;
  box-shadow: var(--maximeheckel-shadow-1);
  padding: 36px 24px;
  transform: none;
  transition: 0.5s;

  h2 {
    color: var(--maximeheckel-colors-typeface-primary);
    margin-bottom: 0px !important;
    letter-spacing: -0.02em;
    margin-block-end: 0px;
  }

  p {
    color: var(--maximeheckel-colors-typeface-secondary);
    margin-top: 1em;
  }
`;


const Featured = styled.div`
  ul {
    display: grid;
    row-gap: 16px;
    grid-template-columns: none;
    grid-template-rows: none;
    grid-auto-flow: row;
    height: inherit;
    margin-left: 0px;
    margin-bottom: 0px;

    li {
      list-style: none;
      position: relative;
      margin-left: -10px;

      a {
        text-decoration: none;

        &:hover {
          ${Bg} {
            opacity: 0.8;
          }

          ${Inner} {
            transform: scale(1.05) translateZ(0px);
          }
        }
      }

      &:nth-child(4n-3) {
        ${Inner} {
          h2 {
            background: linear-gradient(90deg, #8cb1ff 0%, #abe8ff 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
          }
        }

        ${Bg} {
          background: linear-gradient(90deg, #8cb1ff 0%, #abe8ff 100%);
        }
      }

      &:nth-child(4n-2) {
        ${Inner} {
          h2 {
            background: linear-gradient(104.01deg, #9BEBEB 5.51%, #0FA6E9 98.93%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
          }
        }

        ${Bg} {
          background: linear-gradient(104.01deg, #9BEBEB 5.51%, #0FA6E9 98.93%);
        }
      }

      &:nth-child(4n-1) {
        ${Inner} {
          h2 {
            background: linear-gradient(90deg, #ffa0ae 0%, #aacaef 75%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
          }
        }

        ${Bg} {
          background: linear-gradient(90deg, #ffa0ae 0%, #aacaef 75%);
        }
      }

      &:nth-child(4n) {
        ${Inner} {
          h2 {
            background: linear-gradient(90deg, #859EF5 0%, #ffbdd8 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
          }
        }

        ${Bg} {
          background: linear-gradient(90deg, #859EF5 0%, #ffbdd8 100%);
        }
      }
    }
  }
`;

const Articles = styled.div`
  ul {
    display: grid;
    row-gap: 4px;
    grid-template-columns: none;
    grid-template-rows: none;
    grid-auto-flow: row;
    height: inherit;
    margin-left: 0px;
    margin-bottom: 0px;

    li {
      list-style: none;

      a {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding-left: 10px;
        border-radius: 16px;
        margin-left: -10px;
        height: 60px;
        box-shadow: none;
        color: var(--maximeheckel-colors-typeface-primary);
        transition: background-color 0.25s, box-shadow 0.25s, color 0.25s;
        text-decoration: none;
        font-weight: 500;

        &:hover {
          background-color: var(--maximeheckel-colors-foreground);
          box-shadow: var(--maximeheckel-shadow-1);
          color: var(--maximeheckel-colors-brand);
        }
      }
    }
  }
`;
