import { Article as TArticle, styled, SummaryArticle, useFile, useIntl } from 'topwrite';
import Header from '@/components/header';

interface Props {
    article: SummaryArticle;
}

export default function Article({ article }: Props) {

    const file = useFile();
    const mtime = file.getMeta('mtime');

    const intl = useIntl();

    return <>
        <Header title={article.title} />
        <Top>
            <h1>{article.title}</h1>
            <Info>
                <UpdateTime>{intl.formatDate(mtime)} {intl.formatTime(mtime)}</UpdateTime>
            </Info>
        </Top>
        <Body header={null} />
    </>;
}

const Body = styled(TArticle)`
  padding: 20px 0px;
  grid-column: 2 / auto;
  display: grid;
  grid-template-columns:1fr minmax(auto, 700px) 1fr;
  column-gap: 20px;
  color: var(--maxime-colors-typeface-secondary);

  article {
    grid-column: 2 / auto;
    color: var(--maxime-colors-typeface-secondary);

    section {
      h1, h2, h3, h4, h5, h6 {
        a:empty[id] {
          top: -5rem;
        }
      }
    }
  }

  aside {
    position: fixed;
    top: 200px;
    display: flex;
    left: 30px;

    ul {
      li {
        list-style: none;
        font-size: 14px;
        font-weight: 500;
        line-height: 1.5;
        margin-bottom: 0;

        p {
          margin: 0;
        }

        a {
          color: var(--maximeheckel-colors-typeface-tertiary);
          text-decoration: none;
          line-height: 2;

          :hover {
            text-decoration: none;
            color: var(--maximeheckel-colors-brand);
          }
        }
      }
    }

    @media (max-width: 1024px) {
      display: none;
    }

  }
`;

const UpdateTime = styled.span`
  font-size: 14px;
  border-radius: 8px;
  height: 28px;
  align-items: center;
  justify-content: center;
  min-width: 40px;
  font-weight: 500;
  cursor: default;
  user-select: none;
  background: var(--maximeheckel-colors-emphasis);
  color: var(--maximeheckel-colors-brand);
  display: inline-flex;
  padding: 5px 8px;
`;

const Info = styled.div`
  margin-bottom: 30px;
`;

const Top = styled.div`
  display: grid;
  column-gap: 20px;
  grid-template-columns: 1fr minmax(auto, 700px) 1fr;
  grid-template-rows: none;
  grid-auto-flow: row;
  height: inherit;
  padding-top: 248px;
  @media (max-width: 700px) {
    padding-top: 150px;
  }

  & > * {
    align-items: center;
    color: var(--maximeheckel-colors-typeface-primary);
    grid-column: 2 / auto;
  }
`;
